.contacts {
    display: flex;
    align-items: center;
    justify-content: center;

    h1 {
        font-size: 64px;
        margin-bottom: 40px;
    }

    a {
        font-size: 24px;
        font-weight: 400;

        &:hover {
            text-decoration: underline;
        }
    }

    li {
        margin-bottom: 20px;
    }
}

.wrapper {
    padding-top: 16vh;
    max-width: 500px;
}

@media (max-width: 768px) {
    .contacts a {
        font-size: 18px;
    }
}
